import React, { useEffect, useState } from 'react'
import RevokeFeeds from './Components/RevokeFeeds'
import { Container, Image } from 'react-bootstrap';
import HttpClient from '../../../../actions/http-client';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { GET_EXTRA_FEEDS_API, LOGOBRAND, DOMAIN_NAME } from '../../../../actions/api';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { getUserInfo } from '../../../../actions/loginActions';
import { useNavigate } from 'react-router-dom';
import "./limit_excced.scss"
import { SET_EXTRA_LIMITS } from '../../../../reducer/keys';

function LimitExceed() {
    const [feeds, setFeeds] = useState([]);
    const [extraGallery, setExtraGallery] = useState(0)
    const [extraFeeds, setExtraFeeds] = useState(0)
    const [extraPosts, setExtraPosts] = useState(0)
    const [extraProducts, setExtraProducts] = useState(0)
    const [isLoading, setIsLoading] = useState(true)
    const UserRule = useSelector(state => state.authorized?.userData?.user_data, shallowEqual);
    const dispatch = useDispatch()
    const navigation = useNavigate()
    useEffect(() => {
        getFeeds();
    }, [])


    const getFeeds = () => {
        new HttpClient().get(GET_EXTRA_FEEDS_API).then((response) => {
            if (response.data && Object.keys(response.data).length > 0) {
                const { responseData } = response.data;
                if (responseData && Object.keys(responseData).length > 0) {
                    const { wallList, delete_extra_feed, delete_extra_gallery, delete_posts, delete_tagged_products } = responseData;
                    if (delete_extra_gallery > 0) setExtraGallery(delete_extra_gallery)
                    if (delete_extra_feed > 0) setExtraFeeds(delete_extra_feed)
                    if (wallList.length > 0) setFeeds(wallList);
                    if (delete_tagged_products > 0) setExtraProducts(delete_tagged_products)
                    if (delete_posts > 0) setExtraPosts(delete_posts)
                    setIsLoading(false)
                    dispatch({type:SET_EXTRA_LIMITS, payload:responseData})

                }
            }
        }).catch((ex) => {
            setIsLoading(false)
        });
    }

    const deleteWall = (wallId) => {
        const updateFeeds = feeds.filter(feed => feed.wallId != wallId);
        setFeeds(updateFeeds);
        getFeeds()
        dispatch(getUserInfo(() => { }))
        if (parseInt(UserRule?.feeds) <= updateFeeds.length) navigation('/home')

    }

    const deleteFeedFromWall = (wallId, feedIdList) => {
        const toDeleteFeeds = feedIdList.map(feedId => Number(feedId));
        const updateWallFeeds = feeds.map(wall => {
            if (wall.wallId === wallId) {
                const updatedFeedList = wall.feed.filter((item) => toDeleteFeeds.indexOf(item.id) === -1);
                wall.feed = updatedFeedList;
            }
            return wall;
        });
        setFeeds(updateWallFeeds);
        getFeeds()
        dispatch(getUserInfo(() => { }))
        if (parseInt(UserRule?.feeds) <= updateWallFeeds.length) navigation('/home')

    }

    return <div className="d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 flex-position-center full-content">
        <Container>
            <div className='mb-6 f-center'>
                {/* <Image src={baseUrl('media/logo/tagshop-icon.svg')} height={50} width={50} alt='Tagshop' fluid /> */}
                <Image src={LOGOBRAND} height={50} width={50} alt={DOMAIN_NAME} fluid />
            </div>
            <RevokeFeeds feedsList={feeds} extraFeeds={extraFeeds} extraGallery={extraGallery} deleteWall={deleteWall} deleteFeedFromWall={deleteFeedFromWall} extraPosts={extraPosts} extraProducts={extraProducts} isLoading={isLoading}/>
        </Container>
    </div>
}

export default LimitExceed