import React, { useEffect, useState } from "react";
import { onDeleteWall } from "./utils";
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Accordion, OverlayTrigger, Tooltip, Button, Card, Row, Col, Image, Popover } from "react-bootstrap";
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from "../../../../../utils";
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import RevokeList from "./RevokeList";
import RevokeModal from "./RevokeModal";
import { TOAST_SUCCESS } from "../../../../../utils";
import HttpClient from "../../../../../actions/http-client";
import { DELETE_EXTRA_FEED, DELETE_EXTRA_FEED_WALL } from "../../../../../actions/api";
import { GALLERY_WALL_UPDATE } from '../../../../../actions/sceneActions';



const RevokeFeeds = ({ feedsList, deleteWall, deleteFeedFromWall, extraGallery, extraFeeds, extraProducts, extraPosts, isLoading }) => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [accKey, setAccKey] = useState('')
  const deleteExtraFeed = (wallId, feedsList, networkId) => {
    new HttpClient().delete(DELETE_EXTRA_FEED, { data: { wallId: wallId, feedId: feedsList, networkId } }).then((response) => {
      if (response.data && Object.keys(response.data).length > 0) {
        const { message } = response.data;
        TOAST_SUCCESS(message);
        deleteFeedFromWall(wallId, feedsList);
      }
    }).catch((ex) => {

    });
  }

  const deleteExtraFeedWall = (wallData) => dispatch(GALLERY_WALL_UPDATE({ action: 3, name: wallData.wallname, status: 0, autoUpdate: 0, id: wallData.wallId, deleted: 1 }, navigate, ()=>deleteWall(wallData.wallId)))


  useEffect(()=>{
    if((extraPosts || extraProducts) && feedsList.length >0) setAccKey(feedsList[0]?.wallId)
  },[extraProducts, extraPosts])

  return (
    <Card className="limit_excced_ py-4">
      <Card.Body>
        <Row className="me-6 gx-0 px-3">
          {['Gallery', 'Feeds', 'Network', 'Action'].map((text, index) => (
            <Col key={index}>
              <p className={`fw-bold text-nowrap p-2 mb-0 ${text === 'Action' ? 'text-end' : ''}`}>
                {text}
              </p>
            </Col>
          ))}
        </Row>
        <Accordion onSelect={(key) => setAccKey(key)} flush activeKey={accKey}>
          {feedsList.map((feed, i) => {
            const networkIconList = feed?.feed?.length > 0 ? feed.feed.map((network) => network?.Network?.Networkid) : [];
            const feedLength = feed?.feed?.length > 0 || false
            return <Accordion.Item eventKey={feed.wallId} key={feed.wallId} className="mb-4">
              <Accordion.Header className={!feedLength ? 'arrow_disabled' : ''}>
                <Row className="w-100">
                  <Col>
                    <div className="overflow-hidden p-2" style={{ maxWidth: 200 }}>
                      <p className="fw-bold mb-0 line-clamp-2 line-clamp">{feed.wallname}</p>
                    </div>
                  </Col>
                  <Col>
                    <p className="fw-bold text-nowrap p-2 mb-0">{feed.feed.length}</p>
                  </Col>
                  <Col>
                    <p className="fw-bold text-nowrap px-2 mb-0">{networkIconList && networkIconList.length > 0 ?
                      <div className="symbol-group symbol-hover flex-nowrap">
                        {networkIconList.slice(0, 3).map((Item, Index) => {
                          return <OverlayTrigger key={Index} placement='top' overlay={<Tooltip>{GET_NETWORK_NAME(Item)}</Tooltip>}><div className="symbol symbol-34 symbol-circle border">
                            <span className="symbol-label bg-gray-200 fw-bold text-uppercase fs-8">
                              <Image src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(Item)}.svg`)} height={16} width={16} alt={GET_NETWORK_NAME(Item)} fluid />
                            </span>
                          </div></OverlayTrigger>
                        })}
                        {networkIconList.length > 3 ?
                          <OverlayTrigger placement='top' overlay={
                            <Popover>
                              <div className="tooltip_symbol d-flex flex-wrap p-3">
                                {
                                  networkIconList.slice(3, networkIconList.length).map((Item, Index) => (<div key={Index} className="symbol symbol-34 symbol-circle border m-1">
                                    <span className="symbol-label bg-gray-200 fw-bold text-uppercase fs-8">
                                      <Image src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(Item)}.svg`)} height={16} width={16} alt={GET_NETWORK_NAME(Item)} fluid />
                                    </span>
                                  </div>
                                  ))
                                }
                              </div>
                            </Popover>
                          }>
                            <div className="symbol symbol-34 symbol-circle cursor-pointer">
                              <span className="symbol-label bg-primary fs-9 fw-medium">+{networkIconList.length - 3}</span>
                            </div>
                          </OverlayTrigger>
                          : null}
                      </div>
                      : ' -- '}</p>
                  </Col>
                  <Col className="text-end">
                    <OverlayTrigger placement='top' overlay={<Tooltip>Delete</Tooltip>} >
                      <Button variant='icon' size="sm" className="btn-active-primary" aria-label="delete" onClick={() => onDeleteWall(() => deleteExtraFeedWall(feed))}>
                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                      </Button>
                    </OverlayTrigger>
                  </Col>
                </Row>
              </Accordion.Header>
              {feedLength ? <Accordion.Body className="px-0">
                <div className="border rounded-1 p-3 overflow-hidden">
                  <RevokeList feeds={feed.feed} wallId={feed.wallId} deleteExtraFeed={deleteExtraFeed} navigate={navigate}/>
                </div>
              </Accordion.Body> : null}
            </Accordion.Item>
          })}
        </Accordion>
        <RevokeModal isExtra={extraPosts > 0  || extraProducts > 0 ? true : false} isLoading={isLoading}/>
        <div className="mt-5">
          <p className="fs-8 text-muted text-center mb-0">
            You have exceeded your plan's limit. Delete extra <strong>
              {extraGallery > 0 && `Gallery (${extraGallery})`} {extraFeeds > 0 && `Feeds (${extraFeeds})`}
            </strong> for a seamless experience.
          </p>
        </div>

      </Card.Body>
    </Card>
  );
}

export default RevokeFeeds;