import React, { useState } from 'react';
import { Button, Modal, Image } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router';
import { Loader } from '../../CommonComponents/Loader';


function RevokeModal(props) {
    const [show, setShow] = useState(true);
    const navigation = useNavigate()

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const dispatch = useDispatch();

    return (
        <Modal show={show} onHide={handleClose} backdrop="static" size='lg' aria-labelledby="revoke" keyboard={false} centered>
            <Modal.Body style={{minHeight:347}}>
                {props.isLoading ? <Loader/> : 
                <>
                    <Button variant='close' className="position-absolute end-0 top-0 m-5" onClick={handleClose}></Button>
                    <div className="py-lg-8 py-5 text-center">
                        <Image className='mb-6' src={baseUrl('media/images/error/warning.svg')} width="65" height="65" fluid/>
                        <h2 className='h3'>Limit Exceeded: {props.isExtra ? "You Have Extra Posts or Products Tagged!" :  "You're Running Extra Gallery/Feeds!"}</h2>
                        
                        <p className="mb-6 fs-8 text-muted fw-medium">{`Your account has been paused! ${props.isExtra ? "Your feed has reached the maximum limit of posts fetched and products that can be tagged in your current plan. To continue, take action." : "To enjoy uninterrupted services,kindly revoke the additional  gallery/feed or upgrade your plan."} `}</p>
                        <div className="text-center mb-4">
                            <Button variant='primary' className='m-2 btn-min-w' onClick={() => { navigation(`/price`); handleClose() }}>Upgrade Now</Button>
                            <Button variant='outline-primary' className='m-2 btn-min-w' onClick={handleClose}>Revoke</Button>
                        </div>
                        <div className='f-center'>
                            <Button variant='link' onClick={() => { navigation(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :''}`); handleClose() }}>Skip</Button>
                        </div>
                    </div>
                </> }
            </Modal.Body>
        </Modal>
    );
}
export default RevokeModal;