import { onDeleteWall } from "./utils";
import ReactHtmlParser from 'react-html-parser';
import { Container, Card, Table, Tooltip, OverlayTrigger, Button, Form } from 'react-bootstrap'
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";

export default function RevokeList({ feeds, wallId, deleteExtraFeed, navigate}) {

  const getAddFeedsId = (feedsList) => {
    if (feedsList?.length > 0) return feedsList.map((feed) => `${feed.id}`)
  }
  const getNetworkId = (feedsList) => {
    if (feedsList?.length > 0) return feedsList.map((feed) => `${feed.Network.Networkid}`)
  }


  const getfeedTag = (tag) => {
    if (tag) {
      if (tag.includes('<i class')) {
        return <>{ReactHtmlParser(tag)}{" "}</>
      }
      return tag;
    } else return '';
  }
  return (<Table className='mb-0 pb-3' size='lg' responsive>
    <thead className="thead-light">
      <tr>
        <th className="fw-bold">
          Feeds
        </th>
        <th className="fw-bold text-nowrap" style={{ minWidth: 50 }}>Total posts</th>
        <th className="fw-bold text-nowrap pe-6 text-end" style={{ minWidth: 100 }}>
          <Button variant="danger" size='sm' onClick={() => onDeleteWall(() => deleteExtraFeed(wallId, getAddFeedsId(feeds), getNetworkId(feeds)))} wallid={wallId}>
            <FontAwesomeIcon type="solid" icon={'trash-can'} iconClass="me-1" />
            Delete All
          </Button>
        </th>
      </tr>
    </thead>
    <tbody>
      {feeds.length > 0 ? feeds.map((feed, index) => (<tr key={index}>
        <th className="fw-bold text-nowrap" style={{ minWidth: 50 }}>
          <p className="mb-0 line-clamp-2 line-clamp" style={{ maxWidth: 300 }}>{getfeedTag(feed.feedsFilterprefix)}
            {feed.name} - {feed.feedsFiltername}</p>
        </th>
        <th className="fw-bold text-nowrap" style={{ minWidth: 100 }}>
          {`${feed.totalrecords > 1 ? 'Posts' : 'Post'}: ${feed.totalrecords}`}
        </th>
        <td className="text-end pe-6">
          <div className="d-flex align-items-center justify-content-end">
              <Button variant='dark' size="sm" className="me-2" onClick={() => { navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :''}`) }}>
                Moderate
              </Button>
            <OverlayTrigger placement='top' overlay={<Tooltip>Delete</Tooltip>} >
              <Button variant='icon' className="btn-active-primary border-0" aria-label="delete" onClick={() => onDeleteWall(() => deleteExtraFeed(wallId, [`${feed.id}`], [`${feed?.Network?.Networkid}`]))}>
                <FontAwesomeIcon type="solid" icon={'trash-can'} />
              </Button>
            </OverlayTrigger>
          </div>
        </td>
      </tr>
      ))
        : null}
    </tbody>
  </Table>
  );
}